.list {
	border-radius: 4px;
	padding: 3px 0px;
	overflow-y: auto;
	/* background: #fff; */
	margin: 10px;
}

.listItem {
	border-bottom: 1px solid var(--border-color-2);
	background: var(--bg-white);
	padding: 15px;
	display: flex;
	align-items: center;
}

.heading {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

.listItem:last-of-type {
	border: none;
}

.tag {
	border-radius: 4px;
	font-size: 12px;
	line-height: 24px;
	text-align: center;
	margin-right: 19px;
	width: 64px;
	color: #ffac4a !important;
	background-color: rgba(255, 172, 74, 0.1);
	text-transform: capitalize;
}

.transaction__completed {
	margin-left: auto;
	color: #2EC193 !important;
}
.transaction__canceled,
.transaction__declined {
	margin-left: auto;
	color: #ff4a4a !important;
}
.transaction__pending {
	margin-left: auto;
	color: #ffac4a !important;
}

.tag__settled {
	color: #2ec193 !important;
	background-color: rgba(46, 193, 147, 0.1);
}

.tag__declined {
	color: #ff4a4a !important;
	background-color: rgba(255, 74, 74, 0.1);
}

div.bodyStyles {
	padding: 0;
	margin: 0;
	/* background: var(--bg-light); */
}